import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { User } from '../user';
import { UserService } from '../user.service';
import { TodoService } from '../todo.service';
import { Task } from '../task';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css'],
})
export class MainComponent implements OnInit{
  username;  
  taskId;
  todos = [];
  newTodo;
  selectedTodo;
  checkIcon = 'http://todolist-todolist.7e14.starter-us-west-2.openshiftapps.com/media/check.png';
  expiredValue = new Date();
  
  constructor(
    private todoService: TodoService,
    private appUser: UserService,
    public user: User,
    private router: Router,
    public taskClass: Task,
    private cookie: CookieService,
    )
    {
      if (this.cookie.get('isAuthorized') === 'true')
      {
        this.appUser.getCurrentUser();
        this.cookie.delete('ti');
        this.appUser.setLoggedIn(true);
        this.todoService.myToken = this.appUser.getCurrentToken();
        this.appUser.myToken = this.appUser.getCurrentToken();
        if (this.cookie.get('isSuper') === 'true')
          this.user.isSuperuser = true;
        else
          this.user.isSuperuser = false;
        
        this.newTodo = {title: ''};
        this.taskClass.id = null;
        this.getTodos();        
      }  
      else
      { 
        this.appUser.setLoggedIn(false);
        this.router.navigate(['']);
      }
    }

    ngOnInit() {
      this.getTodos();
    }

    editTodo(todo){
      todo.editing = true;
    }

    getTodos() {
      if (this.user.isSuperuser)
      {
        this.todoService.getAllTodos().subscribe(
          data => {
            this.todos = data;
          },
          error => {
            console.log(error);
          }
        );
      }
      else
      {
        this.todoService.getTodosByUser(this.user.id).subscribe(
          data => {
            this.todos = data;
          },
          error => {
            console.log(error);
          }
        );
      }
    }
  
    todoClicked(todo) {
      this.todoService.getTodo(todo.id).subscribe(
        data => {
          this.selectedTodo = data;
          this.taskClass.id = data.id;
          this.appUser.getUsername(this.selectedTodo.user)          
            .subscribe(
              data => {
                this.username = data[0].username.toString();
              }
            )
          this.expiredValue.setHours(this.expiredValue.getHours() + 12)
          this.cookie.set('ti', this.appUser.encryptData(data.id), 
            this.expiredValue)
          this.router.navigate(['/main/tasks', todo.id]);
        },
        error => {
          console.log(error);
        }
      );
      }
    
    toggleCompletion(todo){
      if(todo.is_complete)
        this.inCompleteTodo(todo)
      else
        this.completeTodo(todo)
    }
      
    completeTodo(todo) {
      this.todoService.completeTodo(todo, true)
        .subscribe(
          data => {
            this.getTodos();
            this.newTodo = {title: ''};
          },
          error => {            
            console.log(error);
          }
        );
    }

    

    inCompleteTodo(todo) {
      this.todoService.completeTodo(todo, false)
        .subscribe(
          data => {
            this.getTodos();
            this.newTodo = {title: ''};
          },
          error => {            
            console.log(error);
          }
        );
    }
  
    createTodo() {
      if(this.newTodo.title.length < 8)
      {
        alert('Please input what you want to do, '
          + 'it needs to be at least 8 characters')
        console.log('Minimum length is 8');
      }
      else
      {
        this.todoService.createTodo(this.newTodo.title, 
          this.user.id).subscribe(
          data => {
            this.todos.push(data);
            this.newTodo = {title: ''};
          },
          error => {
            console.log(error);
          }
        );
      }
    }

    deleteTodo(todo) {
      this.todoService.deleteTodo(todo.id).subscribe(
        data => {
          this.getTodos();
          this.newTodo = {title: ''};
        },
        error => {
          console.log(error);
        }
      );
    }

    logout() {
      this.router.navigate(['']);
      this.cookie.delete('isAuthorized');
      this.cookie.delete('ui');
      this.cookie.delete('isSuper');
      this.cookie.delete('ti');
      this.cookie.delete('5Es85xcdwda65sd12sdsaasdascxa654564982xc21');
      this.appUser.setLoggedIn(false);

    }

}

