import { Component} from '@angular/core';
import { User } from '../user';
import { UserService } from '../user.service';
import { Router } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent{

  error = false;
  fileToUpload: File = null;
  arrayData = [];

  constructor(
    public user: User,
    private appUser: UserService,
    private router: Router,
    private cookie: CookieService,
  ) { 
    if (this.cookie.get('isAuthorized') === 'true')
    {
      this.user.id = this.appUser.decryptData(this.cookie.get('ui'));
      this.appUser.myToken = this.appUser.getCurrentToken();
      this.appUser.setLoggedIn(true);  
      this.getUserInfo();
    } 
    else
    {
      this.appUser.setLoggedIn(false);
      this.router.navigate(['']);
    }
  }


  getUserInfo() {
    this.appUser.getUserInfo(this.user.id).subscribe(
      data => {
        this.appUser.getCurrentUser();
        this.user.profileId = data[0].id.toString();
        this.user.dateOfBirth = data[0].dateOfBirth;
        this.user.profileImage = data[0].profileImage;
        console.log('Get user info successfull!');
      },
      error => {            
        console.log(error);
      }
    );
  }

  handleFileInput(files: FileList) {
    this.fileToUpload = files.item(0);
    if (this.fileToUpload.type.includes('image/') != true)
      this.error = false;
    else
      this.error = true;
  }

  updateUserInfo() {
    this.updateUserName();

    if(this.user.dateOfBirth == null && this.fileToUpload.type
        .includes('image/') == true)
      this.updateImage();
    else if(this.fileToUpload == null && this.user.dateOfBirth != null)
      this.updateDateOfBirth();
    else
      this.updateInfo();
    alert('Account information updated!')
    this.getUserInfo();
  }

  updateImage() {
    this.appUser.updateImage(this.user, this.fileToUpload).subscribe(
      data => {
        console.log('updated image')
      },
      error => {            
        console.log(error);
      }
    );
  }

  updateUserName() {
    this.appUser.updateName(this.user).subscribe(
      data => {
        this.arrayData.push(data);
        sessionStorage.setItem('currentUser', JSON.stringify(this.arrayData));
        this.arrayData = [];
        console.log('updated names')
        console.log(data)
      }, 
      error => {            
        console.log(error);
      }
    );
  }

  updateDateOfBirth() {
    this.appUser.updateDateOfBirth(this.user).subscribe(
      data => {
        console.log('updated dob');
      },
      error => {            
        console.log(error)
      }
    );
  }

  updateInfo() {
    this.appUser.updateUserInfo(this.user, this.fileToUpload).subscribe(
      data => {
        console.log('updated dob and image');
      },
      error => {            
        console.log(error);
      }
    );
  }

  goBack() {
    this.router.navigate(['main']);
  }

}

