import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { User } from '../user';
import { UserService } from '../user.service';
import { CookieService } from 'ngx-cookie-service';
import { TodoService } from '../todo.service';
import { HttpHeaders } from '@angular/common/http';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
})
export class LoginComponent implements OnInit {
  
  expiredValue = new Date();
  
  input = {
    username: '',
    password: ''
  };

  constructor(
    public todoService: TodoService,
    private cookie: CookieService,
    public appUser: UserService,
    private router: Router,
    public user: User
    ) { 
      if (this.cookie.get('isAuthorized') === 'true')
      {
        this.appUser.setLoggedIn(true);
        this.router.navigate(['main']);
      }
      else
      {
        this.appUser.setLoggedIn(false);
        this.router.navigate(['']);
      }
    }

  ngOnInit() : void {
      this.input = {
      username: '',
      password: ''
    };
    
  }

  register() {
    this.router.navigate(['register'])
  }

  onLogin() {
    this.appUser.loginUser(this.input).subscribe(
      response => {
        this.expiredValue.setHours(this.expiredValue.getHours() + 12); 
        this.user.username = this.input.username;
        this.user.token = 'Token ' + response.token;
        this.appUser.HttpHeaders = new HttpHeaders({'Authorization': this.user.token});
        this.todoService.HttpHeaders = new HttpHeaders({'Authorization': this.user.token});
        this.appUser.myToken = 'Token ' + response.token;
        this.todoService.myToken = 'Token ' + response.token;
        this.cookie.set('5Es85xcdwda65sd12sdsaasdascxa654564982xc21', 
          this.appUser.encryptData('Token ' + response.token), this.expiredValue)
        this.cookie.set('isAuthorized', 'true', this.expiredValue);
        this.appUser.setLoggedIn(true);
        this.appUser.getUser(this.input.username).subscribe(
          data => {
            this.user.id = data[0].id;
            this.user.firstName = data[0].first_name;
            this.user.lastName = data[0].last_name;
            this.user.username = data[0].username;
            this.user.isSuperuser = data[0].is_superuser;
            this.cookie.set('ui', this.appUser.encryptData(this.user.id), 
              this.expiredValue);
            if (this.user.isSuperuser)
              this.cookie.set('isSuper', 'true', this.expiredValue)
            else
              this.cookie.set('isSuper', 'false', this.expiredValue)
            this.router.navigate(['main']);
          },
          error => {
            console.log(error);
            this.cookie.delete('5Es85xcdwda65sd12sdsaasdascxa654564982xc21');
            this.cookie.delete('isAuthorized');
          }
        )
      },
      error => {
        console.log('error', error);
        alert('Incorrect username or password. Please try again!')
      }
    );
  }  
}