import { Component, OnInit, NgZone } from '@angular/core';
import { Task } from '../task';
import { TodoService } from '../todo.service';
import { UserService } from '../user.service';
import { Router } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-task',
  templateUrl: './task.component.html',
  styleUrls: ['./task.component.css'],
})
export class TaskComponent implements OnInit {
  isLoaded= false;
  taskImages = [];
  task;
  selectedTask;
  files: any;
  editing =false;
  errorImage = false;
  errorLength = false;
  enlarge = false;
  delete = false;

  constructor(
    private todoService: TodoService,
    public taskClass: Task,
    private appUser: UserService,
    private router: Router,
    private cookie: CookieService,
  ) { 
    if (this.cookie.get('isAuthorized') === 'true' && this.cookie.get('ti'))
    {
      this.taskClass.id = this.appUser.decryptData(this.cookie.get('ti'));
      this.todoService.myToken = this.appUser.getCurrentToken();
      this.appUser.myToken = this.appUser.getCurrentToken();
      this.appUser.setLoggedIn(true);
      this.getTask(this.taskClass.id);
    }
    else
    {
      this.appUser.setLoggedIn(false);
      this.router.navigate(['']);
    }
  }

  ngOnInit(){
    if(this.taskClass.id != null)
      this.isLoaded = true;
    else
      this.isLoaded = false;
  } 
        
  

  cancelEditing() {
    this.editing = false;
  }

  editTitle(){
    this.editing = true;
  }

  enlargeImage(task){
    this.enlarge = true;
    this.selectedTask = task;
  }

  deleteImage(task) {
    this.delete = true;
    this.selectedTask = task;
  }

  dropped(event) {
    event.preventDefault();
    this.files = event.dataTransfer.files;
    if (this.files.length > 0)
    {
      this.errorLength = false;
      for ( let file of this.files)
      {
        if(file.type.includes("image/") == true)
        {  
          this.errorImage = false;
          console.log(this.files);
          this.uploadImage(file, file.name);
        }
        else
        {  
          this.errorImage = true;
          console.log(this.files);
        }
      }
    }
    else
    {
      this.errorLength = true;
    }
  }
  
  

  uploadImage(file, path) {
    this.todoService.todoImage(this.taskClass.id, file, path).subscribe(
      data => {
        this.getTask(this.taskClass.id);
      },
      error => {            
        console.log(error);
      }
    );
  }

  getTask(id){
    this.todoService.getTodo(id).subscribe(
      data => {
        this.task = data;
        console.log('Get task detail succeed')
        this.appUser.getUsername(this.task.user)          
            .subscribe(
              data => {
                this.task.username = data[0].username.toString();
              }
            );
        this.getImage(data.id);
        if(this.task.is_complete)
          this.task.status = 'Completed'
        else
          this.task.status = 'Has not completed'
      },
      error => {            
        console.log(error);
      }
    );
  }

  getImage(id) {
    this.todoService.getImage(id).subscribe(
      data => {
        this.taskImages = data;
      },
      error => {
        console.log(error);
      }
    );
  }

  fileOver(event){
    event.stopPropagation();
    event.preventDefault();
  }

  updateTodo(task, editedTitle) {
    this.editing = false;
    if (editedTitle.length < 8){
      alert('Please input what you want to update to, '
        + 'it needs to be at least 8 characters')
    }
    else {
      task.title = editedTitle;
      this.todoService.updateTodo(task).subscribe(
        data => {
          this.getTask(this.task.id);
        },
        error => {            
          console.log(error);
        }
      );
    }  
  }


  goBack() {
    this.router.navigate(['main']);
  }

  closeEnlarge() {
    this.enlarge = false;
  }

  selectedYes(){
    this.todoService.deleteTaskImage(this.selectedTask.id).subscribe(
      data => {
        console.log('image deleted')
        this.getImage(this.task.id);
        this.delete = false;
      },
      error => {            
        console.log(error);
      }
    );
  }

  selectedNo(){
    this.delete = false;
  }

}
